.coupon-list {
  width: 90%;
  margin: 10px auto;
  height: 150px;
  box-shadow: 2px 2px 2px #f1f1f1;
  border: 1px solid #f1f1f1;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  padding: 5px;
}
.coupon-list .coupon-left {
    width: 30%;
    height: 150px;
    float: left;
    text-align: center;
}
.coupon-list .coupon-left span {
      display: block;
}
.coupon-list .coupon-left span:first-child {
      font-size: 24px;
      color: #f60;
      margin-top: 40px;
      margin-bottom: 5px;
}
.coupon-list .coupon-center {
    width: 50%;
    height: 150px;
    float: left;
}
.coupon-list .coupon-center h3 {
      color: #000;
      font-weight: 500;
      font-size: 18px;
}
.coupon-list .coupon-right {
    width: 20%;
    height: 150px;
    float: left;
    text-align: center;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    padding-top: 60px;
}
.el-breadcrumb {
  line-height: 4;
  padding-left: 20px;
  background-color: #ffffff;
}
#storeOrder .contentBody {
  margin: 20px;
  background-color: #ffffff;
  padding: 20px;
}
#storeOrder .contentBody .el-pagination {
    padding-top: 10px;
}
#storeOrder .contentBody .el-aside {
    width: auto;
    height: 700px;
}
#storeOrder .boxContent {
  text-align: left;
  display: flex;
  width: 100%;
  flex-wrap: wrap;
  justify-content: space-between;
}
#storeOrder .boxContent .zhuotai {
    border: 1px solid #000000;
    border-radius: 6px;
}
#storeOrder .boxContent .title {
    padding: 10px;
}
#storeOrder .boxContent .self-card {
    cursor: pointer;
    width: 29%;
    color: #ffffff;
    margin: 5px 20px;
    height: 200px;
    overflow-y: auto;
}
#storeOrder .boxContent .self-card-red {
    background-color: #e16870;
}
#storeOrder .boxContent .self-card-green {
    background-color: #35B178;
}
#storeOrder .boxContent .self-card-gray {
    background-color: #909399;
}
#storeOrder .boxContent:after {
  content: "";
  display: block;
  width: 29%;
  margin: 5px 20px;
}
#storeOrder .largeFont {
  font-size: 24px;
}
.redColor {
  color: #e16870;
}
.btn {
  line-height: 30px;
  background-color: #35B178;
  color: #ffffff;
  border-radius: 4px;
  text-align: center;
  cursor: pointer;
}
